.d-flex{display:flex}.d-flex.justify-content-start{justify-content:flex-start}.d-flex.justify-content-center{justify-content:center}.d-flex.justify-content-end{justify-content:flex-end}.d-flex.justify-content-between{justify-content:space-between}.d-flex.align-items-flex-start{align-items:flex-start}.d-flex.align-items-center{align-items:center}.d-flex.align-items-flex-end{align-items:flex-end}*{margin:0;padding:0}html{height:100%}body{color:white;text-shadow:0 0 5px slateblue;width:100%;height:100%;font-family:sans-serif;background:radial-gradient(ellipse at center, #F7F7F7 0%, #F9F686 26%, #3295FF 100%) no-repeat center;background-size:100%;position:relative}#imgContainer{position:absolute;left:0;top:0;opacity:0;width:100%;background-size:100%;background-repeat:no-repeat;height:100%;transition:opacity 2s;z-index:-1}#imgContainer img{width:100%;height:100%}.container{display:flex;justify-content:center;width:100%;height:100%;align-items:center}.container>div{width:640px;padding:20px;background:rgba(255,255,255,0.2);border-radius:5px}.title{font-size:80px}.row{width:80%;margin:10px auto}.line{height:5px;background:red}.line-mm{transition:width 1s linear}.line-ms{transition:opacity 0.1s linear}.row>.d-flex>div{font-size:40px}.row>.d-flex>div:first-child{flex-basis:40%}.row>.d-flex>div:nth-child(2){flex-basis:10%}.row>.d-flex>div:nth-child(3){flex-basis:50%}
